.usersList {
	height: 80vh;
	overflow-y: auto;
	padding: 0px 0px;
}

.user {
	padding: 4px 8px;
	cursor: pointer;
	list-style: none;
	border-radius: 4px;
	margin-right: 8px;

	&:hover {
		background-color: #181e27;
	}
}

.selected {
	background-color: rgb(48, 48, 119) !important;
}